import { NextRequest, NextResponse } from 'next/server';
import dbConnect from '@/lib/dbConnect';
import CapAlert from '@/models/cap-alert-model';

// GET: Fetch all alerts
export async function GET() {
  try {
    await dbConnect(); // ✅ Fixed
    const alerts = await CapAlert.find().sort({ createdAt: -1 });

    return NextResponse.json({ success: true, data: alerts }, { status: 200 });
  } catch (error: any) {
    console.error('[GET_CAP_ALERT_ERROR]', error);
    return NextResponse.json(
      {
        success: false,
        message: 'Error while fetching CAP alerts.',
        error: error.message,
      },
      { status: 500 }
    );
  }
}

// POST: Save a new alert
export async function POST(req: NextRequest) {
  try {
    await dbConnect(); // ✅ Fixed
    const body = await req.json();

    if (!body.identifier || !body.sender || !body.sent || !Array.isArray(body.info)) {
      return NextResponse.json(
        {
          success: false,
          message: 'Missing required fields: identifier, sender, sent, or info[]',
        },
        { status: 400 }
      );
    }

    const newAlert = new CapAlert(body);
    const saved = await newAlert.save();

    return NextResponse.json(
      {
        success: true,
        message: 'CAP Alert saved successfully.',
        data: saved,
      },
      { status: 201 }
    );
  } catch (error: any) {
    console.error('[POST_CAP_ALERT_ERROR]', error);
    return NextResponse.json(
      {
        success: false,
        message: 'Error while saving CAP alert.',
        error: error.message,
      },
      { status: 500 }
    );
  }
}
