import mongoose from 'mongoose';

const capAlertSchema = new mongoose.Schema(
  {
    identifier: String,
    sender: String,
    sent: String,
    status: String,
    msgType: String,
    scope: String,
    source: String,
    restriction: String,
    addresses: String,
    code: [String],
    note: String,
    references: String,
    incidents: String,
    info: [
      {
        language: String,
        category: [String],
        event: String,
        responseType: [String],
        urgency: String,
        severity: String,
        certainty: String,
        audience: String,
        eventCode: [
          {
            valueName: String,
            value: String,
          },
        ],
        effective: String,
        onset: String,
        expires: String,
        senderName: String,
        headline: String,
        description: String,
        instruction: String,
        web: String,
        contact: String,
        parameter: [
          {
            valueName: String,
            value: String,
          },
        ],
        area: [
          {
            areaDesc: String,
            polygon: [String],
            circle: [String],
            geocode: [
              {
                valueName: String,
                value: String,
              },
            ],
            altitude: String,
            ceiling: String,
          },
        ],
      },
    ],
  },
  { timestamps: true }
);

export default mongoose.models.CapAlert || mongoose.model('CapAlert', capAlertSchema);
